.cardslist-center {
    height: 100%;
    list-style-type: none;
}

@media screen and (min-width: 600px) {
    .cardslist-center {
        color: rgb(0, 0, 0);
        margin: auto auto 3%;
        width: 60vw;
    }
}

@media screen and (max-width: 600px) {
    .cardslist-center {
        color: rgb(0, 0, 0);
        padding: 1em;
        margin: auto auto 3vh;
        width: 93vw;
    }
}

img {
    width: 100%;
    height: auto;
}