.center {
    justify-content: center;
    align-items: center;
    height:100vh;  
}

.card-box {
    margin: 1rem;
    background-color: #D9D9D9;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding: 1rem 1.5rem 1rem 1.5rem;
    font-family: Helvetica, Arial, sans-serif;
    box-shadow: 0 2px 20px rgba(234, 78, 240, 0.2);
}